.iveBeenUsing {
  align-self: stretch;
  position: relative;
  line-height: 32px;
  font-weight: 500;
}
.frameIcon {
  position: relative;
  width: 20px;
  height: 20px;
  overflow: hidden;
  flex-shrink: 0;
}
.frameParent {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-9xs);
}
.frameChild {
  position: relative;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  object-fit: cover;
}
.janeSmith {
  position: relative;
  font-weight: 600;
}
.freelanceDesigner {
  position: relative;
  font-size: var(--font-size-sm);
}
.ellipseParent,
.iveBeenUsingThisWebHostiParent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-9xs);
  text-align: left;
  font-size: var(--font-size-lg);
}
.iveBeenUsingThisWebHostiParent {
  border-radius: var(--br-7xs);
  background-color: var(--color-gray-300);
  width: 360px;
  padding: var(--padding-21xl) var(--padding-11xl);
  box-sizing: border-box;
  gap: var(--gap-5xl);
  text-align: center;
  font-size: var(--font-size-base);
  color: var(--white);
  font-family: var(--font-inter);
}
